          "future_aim": "",
          "future_plan": "",
          "income_status": "",
          "industry": "",
          "industry_resource": "",
          "investors": "",
          "main_income": "",
          "market_capacity": "",
          "market_proficiency": "",
          "market_rate": "",
          "needs_desc": "",
          "needs_support": "",
          "organization": "",
          "our_resource": "",
          "pain_point": "",
          "potential_income": "",
          "product_status": "",
          "project_name": "",
          "recent_plan": "",
          "risk": "",
          "risk_detail": "",
          "rival": "",
          "score_evaluation": "",
          "score_industry": "",
          "score_mode": "",
          "score_needs": "",
          "score_product": "",
          "score_resource": "",
          "score_risk": "",
          "score_team": "",
          "source": "",
          "start_from": "",
          "stock_structure": "",
          "tags": "",
          "team_desc": "",
          "tech_evaluation": "",
          "timestamp": "",
          "valuation": ""
        },
        fileData: {},
        // options: [{
        //   value: 'zhinan',
        //   label: '',
        //   children: [{
        //     value: 'shejiyuanze',
        //     label: '',
        //     children: [{
        //       value: 'yizhi',
        //       label: ''
        //     }, {
        //       value: 'fankui',
        //       label: ''
        //     }, {
        //       value: 'xiaolv',
        //       label: ''
        //     }, {
        //       value: 'kekong',
        //       label: ''
        //     }]
        //   }, {
        //     value: 'daohang',
        //     label: '',
        //     children: [{
        //       value: 'cexiangdaohang',
        //       label: ''
        //     }, {
        //       value: 'dingbudaohang',
        //       label: ''
        //     }]
        //   }]
        // }]
        selectProps: {
          value: '',
          label: ''
        },
        regions: [],
        citys: []
      }
    },
    created() {
      this.region()
    },
    methods: {
      //  
      uploadFile(ele) {
        upload(ele.target)
      },
      //  BP
      submitBP() {
        var _this = this
        this.form.bp_url = $("#hiddens").val()
        if (this.form.start_from === '') {
          this.form.start_from = ''
        } else {
          this.form.start_from = Date.parse(new Date(this.form.start_from))
        }
        $.ajax({
          url: '/admin/api/v1/bps',
          type: 'post',
          contentType: 'application/json',
          data: JSON.stringify(this.form),
          success: function(result) {
            _this.$message({
              message: result.message,
              type: 'success'
            })
          }
        })
      },
      //  
      region() {
        var _this = this
        $.ajax({
          url: '/main/api/v1/region?page=1',
          success: function(result) {
            var data = result.result
            _this.regions = data
            _this.selectProps = {
              value: 'area_id',
              label: 'name'
            }
          }
        })
      },
      //  
      city(id) {
        var _this = this
        $.ajax({
          url: '/main/api/v1/region?page=1&region_id=eead4c86-a579-4d83-8e96-7064882bdabf',
          success: function(result) {
            var data = result.result
            _this.citys = data
          }
        })
      },
    }
  }
</script>

